				show:false,
				aaa: false
			}
		},
		created() {
			this.$axios.get("/db/wodeguanzhu.json")
			.then(res=>{
				console.log(res)
				this.wodeguanzhu = res.data.wodeguanzhu
				console.log(this.wodeguanzhu)
			})
		},
		methods:{
			guanzhu(){
				this.show = true
				this.aaa = true
			},
			guanbi(){
				this.show = false
				this.aaa = false	
			}
		}
		
	}
</script>

